$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-secondary: #101d2c;
.footer {
  width: 100%;
  position: relative;
  background-color: $color-secondary;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;

  .logo {
    width: 15rem;
    height: 15rem;
  }

  .main_section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  h2 {
    color: $color-primary;
    margin-bottom: 1rem;
  }
  .about {
    width: 50%;
    color: white;
    font-size: 1.4rem;
  }
  .contact {
    width: 30%;
    color: white;
    font-size: 1.4rem;
  }
  .icons {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .icon {
    font-size: 3rem;
    color: white;
    transition: all 0.3s;
    &:hover {
      transform: scale(1.2);
    }
  }
}
@media (max-width: 1024px) {
  .footer {
    position: relative;

    .main_section {
      padding: 2rem;
      flex-direction: column-reverse;
      width: 100%;
    }
    .icons {
      display: flex;
      flex-direction: row;
      width: 100%;
      justify-content: space-around;
      padding: 2rem;
    }
    .icon {
      font-size: 5rem;
    }
    .about {
      margin-top: 2rem;
      margin-bottom: 2rem;
      width: 100%;
      font-size: 2rem;
    }
    .contact {
      width: 100%;
      color: white;
      font-size: 2rem;
    }
    .logo {
      position: absolute;
      right: 2rem;
      top: 2rem;
    }
  }
}
